import React from 'react'
import { useCart } from 'react-use-cart'

function Header() {
  // 1. تصحيح: نقل useCart داخل المكون الرئيسي
  // كان الخطأ في استدعاء useCart خارج المكون function Header()
  const { totalItems } = useCart();
  
  return (
    <div>
      <section className="banner-section d-flex justify-content-center align-items-end">
        <div className="section-overlay" />
        <div className="container">
          <div className="row">
            <div className="col-lg-7 col-12">
              <h1 className="text-white mb-lg-0">Market 🛍️
                <span className="position-absolute top-0 fs-5 translate-middle badge rounded-pill bg-danger">
                  {totalItems}</span>
              </h1>
            </div>
            <div className="col-lg-4 col-12 d-flex justify-content-lg-end align-items-center ms-auto">
              <nav aria-label="breadcrumb">
                <ol className="breadcrumb justify-content-center">
                  <li className="breadcrumb-item">
                    <a href="/">Home</a>
                  </li>
                  {/* 2. تصحيح: إضافة position-relative للعنصر الأب */}
                  {/* هذا ضروري لتحديد موقع العداد بشكل صحيح بالنسبة للعنصر الأب */}
                  <li className="breadcrumb-item active position-relative" aria-current="page">
                    Market 🛍️
                  </li>
                </ol>
              </nav>
            </div>
          </div>
        </div>
      </section>
    </div>
  )
}

export default Header