import React from 'react'
import Navbar from '../Components/Navbar'
import Footer from '../Components/Footer'
import MainSections from './MainSections'
import SubNavbar from '../Components/SubNavbar'

function AllContact() {
  return (
    <div>
      <SubNavbar />
      <Navbar />
      <MainSections />
      <Footer />
    </div>
  )
}

export default AllContact