import React from 'react';
import ReactDOM from 'react-dom';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import App from "./App";
import { AllData } from "./Data/AllData";
import About from "./AboutUs/About";
import AllContact from "./Contact/AllContact";
import AllStore from "./Store/AllStore";
import AllCart from "./Cart/AllCart";
import AllServices from "./Services/AllServices";
import AllServiceDetail from "./Services/AllServiceDetail";
import AllComingSoon from "./ComingSoon/AllComingSoon";
import AllGetStarted from "./GetStarted/AllGetStarted";
import AllShop from './Shop/AllShop';

ReactDOM.render(
  <AllData>
    <Router>
      <Switch>
        <Route exact path="/" component={App} />
        <Route path="/about" component={About} />
        <Route path="/contact" component={AllContact} />
        <Route path="/store" component={AllStore} />
        <Route path="/cart" component={AllCart} />
        <Route path="/services" component={AllServices} />
        <Route path="/services-detail" component={AllServiceDetail} />
        <Route path="/comingsoon" component={AllComingSoon} />
        <Route path="/getstarted" component={AllGetStarted} />
        <Route path="/shop" component={AllShop} />
      </Switch>
    </Router>
  </AllData>,
  document.getElementById('root')
);
