import React from 'react'

function MainSections() {
    return (
        <div>
            <main>
                <section className="banner-section d-flex justify-content-center align-items-end">
                    <div className="section-overlay" />
                    <div className="container">
                        <div className="row">
                            <div className="col-lg-7 col-12">
                                <h1 className="text-white mb-lg-0">Contact</h1>
                            </div>
                            <div className="col-lg-4 col-12 d-flex justify-content-lg-end align-items-center ms-auto">
                                <nav aria-label="breadcrumb">
                                    <ol className="breadcrumb justify-content-center">
                                        <li className="breadcrumb-item">
                                            <a href="index.html">Home</a>
                                        </li>
                                        <li className="breadcrumb-item active" aria-current="page">
                                            Contact
                                        </li>
                                    </ol>
                                </nav>
                            </div>
                        </div>
                    </div>
                </section>
                <section className="contact-section section-padding">
                    <div className="container">
                        <div className="row">
                            <div className="col-lg-5 col-12 me-auto mb-lg-0 mb-5">
                                <h2 className="my-3">We're happy to help</h2>
                                <p>
                                    Best Cleaning Service is ready to serve you. Clean Work is a
                                    professional website layout for your business.
                                </p>
                                <div className="contact-info bg-white shadow-lg">
                                    <h3 className="mb-4">Contact Information</h3>
                                    <h5 className="d-flex mt-3 mb-3">
                                        <i className="bi-geo-alt-fill custom-icon me-3" />
                                        Akershusstranda 20, 0150 Oslo, Norway
                                    </h5>
                                    <h5 className="d-flex mb-3">
                                        <i className="bi-telephone-fill custom-icon me-3" />
                                        <a href="tel: 110-220-9800">110-220-9800</a>
                                    </h5>
                                    <h5 className="d-flex">
                                        <i className="bi-envelope-fill custom-icon me-3" />
                                        <a href="mailto:info@company.com">info@company.com</a>
                                    </h5>
                                </div>
                            </div>
                            <div className="col-lg-6 col-12">
                                <form
                                    className="custom-form consulting-form bg-white shadow-lg mb-5 mb-lg-0"
                                    action="#"
                                    method="post"
                                    role="form"
                                >
                                    <div className="consulting-form-header d-flex align-items-center">
                                        <h3 className="mb-4">Get a Free Quotation</h3>
                                    </div>
                                    <div className="consulting-form-body">
                                        <div className="row">
                                            <div className="col-lg-6 col-md-6 col-12">
                                                <input
                                                    type="text"
                                                    name="volunteer-name"
                                                    id="volunteer-name"
                                                    className="form-control"
                                                    placeholder="John Walker"
                                                    required=""
                                                />
                                            </div>
                                            <div className="col-lg-6 col-md-6 col-12">
                                                <input
                                                    type="email"
                                                    name="volunteer-email"
                                                    id="volunteer-email"
                                                    pattern="[^ @]*@[^ @]*"
                                                    className="form-control"
                                                    placeholder="yours@gmail.com"
                                                    required=""
                                                />
                                            </div>
                                        </div>
                                        <select
                                            className="form-select form-control"
                                            name="job-salary"
                                            id="job-salary"
                                            aria-label="Default select example"
                                        >
                                            <option selected="">Service Type</option>
                                            <option value={1}>House cleaning</option>
                                            <option value={2}>Car washing</option>
                                            <option value={2}>Office cleaning</option>
                                            <option value={2}>Factory cleaning</option>
                                        </select>
                                        <textarea
                                            name="volunteer-message"
                                            rows={3}
                                            className="form-control"
                                            id="volunteer-message"
                                            placeholder="Comment (Optional)"
                                            defaultValue={""}
                                        />
                                        <div className="col-lg-6 col-md-10 col-8 mx-auto">
                                            <button type="submit" className="form-control">
                                                Submit Request
                                            </button>
                                        </div>
                                    </div>
                                </form>
                            </div>
                        </div>
                    </div>
                </section>
            </main>

        </div>
    )
}

export default MainSections