import { CartProvider, useCart } from "react-use-cart";
import Footer from "../Components/Footer";
import Header from "./Header";
import Navbar from "../Components/Navbar";
import SubNavbar from "../Components/SubNavbar";

function Cart() {
  const {
    isEmpty,
    totalUniqueItems,
    items,
    updateItemQuantity,
    cartTotal,
    removeItem,
  } = useCart();

  if (isEmpty) {
    return (
      <div className="container text-center mt-5">
        <div className="alert alert-warning shadow-sm" role="alert">
          السلة فارغة حالياً
        </div>
      </div>
    );
  }

  return (
    <div className="container my-5">
      {/* عنوان الصفحة في منتصف الفورم كونترول */}
      <div className="row justify-content-center mb-4">
        <div className="col-md-8 text-center">
          <div className="card shadow-sm border-0 bg-light">
            <div className="card-body py-3">
              <h1 className="mb-0 text-primary">عربة التسوق</h1>
              <small className="text-muted" style={{ fontSize: '1.1rem' }}>( {totalUniqueItems} ) منتج في السلة</small>
            </div>
          </div>
        </div>
      </div>

      <div className="row">
        {/* قسم المنتجات */}
        <div className="col-lg-8">
          <div className="card shadow-lg border-0 rounded-lg">
            <div className="card-body p-0">
              {items.map((item) => (
                <div key={item.id} className="p-4 border-bottom">
                  <div className="row align-items-center">
                    {/* صورة المنتج */}
                    <div className="col-md-2">
                      <img
                        src={item.image || "https://via.placeholder.com/150"}
                        alt={item.title}
                        className="img-fluid rounded-circle shadow-sm"
                        style={{
                          width: '90px',
                          height: '90px',
                          objectFit: 'cover',
                          border: '2px solid #f8f9fa'
                        }}
                      />
                    </div>

                    {/* معلومات المنتج */}
                    <div className="col-md-5">
                      <h5 className="mb-1 font-weight-bold">{item.title}</h5>
                      <p className="text-muted mb-2">السعر: {item.price} جنية للوحدة</p>
                      <button
                        onClick={() => removeItem(item.id)}
                        className="btn btn-link text-danger p-0 shadow-none"
                      >
                        <i className="fas fa-trash-alt mr-1"></i> إزالة من السلة
                      </button>
                    </div>

                    {/* أزرار الكمية */}
                    <div className="col-md-3">
                      <div className="d-flex align-items-center shadow-sm rounded-pill bg-white p-1" style={{ width: 'fit-content' }}>
                        <button
                          className="btn btn-danger px-3 py-1 rounded-pill shadow-sm"
                          onClick={() => updateItemQuantity(item.id, item.quantity - 1)}
                        >
                          -
                        </button>
                        <span className="mx-3 font-weight-bold" style={{ minWidth: '30px', textAlign: 'center' }}>
                          {item.quantity}
                        </span>
                        <button
                          className="btn btn-success px-3 py-1 rounded-pill shadow-sm"
                          onClick={() => updateItemQuantity(item.id, item.quantity + 1)}
                        >
                          +
                        </button>
                      </div>
                    </div>

                    {/* السعر الإجمالي للمنتج */}
                    <div className="col-md-2 text-right">
                      <h6 className="mb-0 font-weight-bold text-primary">
                        {Math.ceil(item.price * item.quantity)} جنية
                      </h6>
                    </div>
                  </div>
                </div>
              ))}
            </div>
          </div>
        </div>

        {/* قسم الملخص والدفع */}
        <div className="col-lg-4 mt-4 mt-lg-0">
          <div className="card shadow-lg border-0 rounded-lg sticky-top" style={{ top: '20px' }}>
            <div className="card-header bg-white border-0 rounded-top">
              <h4 className="form-control btn btn-primary btn-block rounded-pill py-3 shadow-lg"
                style={{
                  background: 'linear-gradient(to right, #4e73df, #224abe)',
                  border: 'none',
                  fontSize: '1.1rem'
                }}>تفاصيل الطلب</h4>
            </div>

            <div className="card-body">
              <div className="mb-4">
                <div className="d-flex justify-content-between mb-2">
                  <span className="text-muted">الإجمالي المؤقت:</span>
                  <span className="font-weight-bold">{Math.ceil(cartTotal)} جنية</span>
                </div>
                <div className="d-flex justify-content-between mb-2">
                  <span className="text-muted">تكاليف التوصيل:</span>
                  <span className="text-success font-weight-bold">مجاني</span>
                </div>
                <hr className="my-3" />
                <div className="d-flex justify-content-between">
                  <span className="font-weight-bold">المبلغ النهائي:</span>
                  <span className="font-weight-bold text-primary h5 mb-0">
                    {Math.ceil(cartTotal)} جنية
                  </span>
                </div>
              </div>

              {/* نموذج الدفع */}
              <form className="mt-4">
                <div className="form-group mb-3">
                  <label htmlFor="address" className="font-weight-bold">عنوان التوصيل</label>
                  <input
                    type="text"
                    className="form-control rounded-pill shadow-sm py-3"
                    id="address"
                    placeholder="أدخل عنوان التوصيل بالتفصيل"
                    required
                  />
                </div>

                <div className="form-group mb-4">
                  <label htmlFor="phone" className="font-weight-bold">رقم الهاتف</label>
                  <input
                    type="tel"
                    className="form-control rounded-pill shadow-sm py-3"
                    id="phone"
                    placeholder="مثال: 01234567890"
                    required
                  />
                </div>

                <button
                  type="submit"
                  className="btn btn-primary btn-block rounded-pill py-3 shadow-lg"
                  style={{
                    background: 'linear-gradient(to right, #4e73df, #224abe)',
                    border: 'none',
                    fontSize: '1.4rem'
                  }}
                >
                  <i className="fas fa-credit-card mr-2"></i>
                  تأكيد الطلب والدفع
                </button>
              </form>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

function AllCart() {
  return (
    <CartProvider>
      <SubNavbar />
      <Navbar />
      <Header />
      <Cart />
      <Footer />
    </CartProvider>
  );
}

export default AllCart;