import React, { useState } from 'react';

function GetStarted() {
    const [formData, setFormData] = useState({ name: '', email: '', password: '', confirmPassword: '' });
    const [submitted, setSubmitted] = useState(false);

    const handleChange = (e) => {
        setFormData({ ...formData, [e.target.name]: e.target.value });
    };

    const handleSubmit = (e) => {
        e.preventDefault();
        const { name, email, password, confirmPassword } = formData;
        if (name && email && password && confirmPassword && password === confirmPassword) {
            setSubmitted(true);
        }
    };

    return (
        <div>
            {/* Banner */}
            <section className="banner-section d-flex justify-content-center align-items-end bg-primary py-5 position-relative">
                <div className="section-overlay position-absolute top-0 start-0 w-100 h-100" style={{ backgroundColor: 'rgba(0, 0, 0, 0.5)' }} />
                <div className="container position-relative text-white">
                    <div className="row align-items-end">
                        <div className="col-lg-7 col-12">
                            <h1 className="display-4 fw-bold">Sign Up</h1>
                        </div>
                        <div className="col-lg-5 col-12 d-flex justify-content-lg-end align-items-center ms-auto">
                            <nav aria-label="breadcrumb">
                                <ol className="breadcrumb mb-0">
                                    <li className="breadcrumb-item">
                                        <a href="/" className="text-white text-decoration-none">Home</a>
                                    </li>
                                    <li className="breadcrumb-item active text-white" aria-current="page">
                                        Sign Up
                                    </li>
                                </ol>
                            </nav>
                        </div>
                    </div>
                </div>
            </section>

            {/* Form Section */}
            <section className="py-5 bg-light">
                <div className="container">
                    <div className="row justify-content-center">
                        <div className="col-lg-6">
                            {!submitted ? (
                                <div className="card shadow border-0">
                                    <div className="card-body p-4">
                                        <h3 className="mb-4 text-center">Create Your Account</h3>
                                        <form onSubmit={handleSubmit}>
                                            <div className="form-floating mb-3">
                                                <input
                                                    type="text"
                                                    className="form-control"
                                                    id="name"
                                                    name="name"
                                                    placeholder="Full Name"
                                                    value={formData.name}
                                                    onChange={handleChange}
                                                    required
                                                />
                                                <label htmlFor="name">Full Name</label>
                                            </div>
                                            <div className="form-floating mb-3">
                                                <input
                                                    type="email"
                                                    className="form-control"
                                                    id="email"
                                                    name="email"
                                                    placeholder="Email Address"
                                                    value={formData.email}
                                                    onChange={handleChange}
                                                    required
                                                />
                                                <label htmlFor="email">Email Address</label>
                                            </div>
                                            <div className="form-floating mb-3">
                                                <input
                                                    type="password"
                                                    className="form-control"
                                                    id="password"
                                                    name="password"
                                                    placeholder="Password"
                                                    value={formData.password}
                                                    onChange={handleChange}
                                                    required
                                                />
                                                <label htmlFor="password">Password</label>
                                            </div>
                                            <div className="form-floating mb-4">
                                                <input
                                                    type="password"
                                                    className="form-control"
                                                    id="confirmPassword"
                                                    name="confirmPassword"
                                                    placeholder="Confirm Password"
                                                    value={formData.confirmPassword}
                                                    onChange={handleChange}
                                                    required
                                                />
                                                <label htmlFor="confirmPassword">Confirm Password</label>
                                            </div>
                                            <button type="submit" className="btn btn-primary w-100 py-2">Create Account</button>
                                        </form>
                                    </div>
                                </div>
                            ) : (
                                <div className="card shadow text-center p-5">
                                    <div className="d-flex flex-column align-items-center">
                                        <svg
                                            xmlns="http://www.w3.org/2000/svg"
                                            width="72"
                                            height="72"
                                            fill="green"
                                            className="bi bi-check-circle-fill mb-3"
                                            viewBox="0 0 16 16"
                                        >
                                            <path d="M16 8A8 8 0 1 1 0 8a8 8 0 0 1 16 0zM6.97 11.03a.75.75 0 0 0 1.08 0l4.243-4.242a.75.75 0 1 0-1.06-1.061L7.5 9.439 5.808 7.75a.75.75 0 1 0-1.06 1.061l2.222 2.219z" />
                                        </svg>
                                        <h4 className="text-success">Account created successfully!</h4>
                                    </div>
                                </div>
                            )}
                        </div>
                    </div>
                </div>
            </section>
        </div>
    );
}

export default GetStarted;
