import React from 'react'
import { Link } from 'react-router-dom' // تعديل هنا

function ServicesDetail() {
    return (
        <div>
            <header className="site-header">
                <div className="container">
                    <div className="row">
                        <div className="col-lg-12 col-12 d-flex flex-wrap">
                            <p className="d-flex me-4 mb-0">
                                <i className="bi-house-fill me-2" />
                                One-Stop Cleaning Service
                            </p>
                            <p className="d-flex d-lg-block d-md-block d-none me-4 mb-0">
                                <i className="bi-clock-fill me-2" />
                                <strong className="me-2">Mon - Fri</strong> 8:00 AM - 5:30 PM
                            </p>
                            <p className="site-header-icon-wrap text-white d-flex mb-0 ms-auto">
                                <i className="site-header-icon bi-whatsapp me-2" />
                                <a href="tel: 110-220-9800" className="text-white">
                                    110 220 9800
                                </a>
                            </p>
                        </div>
                    </div>
                </div>
            </header>

            <main>
                <section className="banner-section d-flex justify-content-center align-items-end">
                    <div className="section-overlay" />
                    <div className="container">
                        <div className="row">
                            <div className="col-lg-7 col-12">
                                <h1 className="text-white mb-lg-0">Office Cleaning</h1>
                            </div>
                            <div className="col-lg-4 col-12 d-flex justify-content-lg-end align-items-center ms-auto">
                                <nav aria-label="breadcrumb">
                                    <ol className="breadcrumb justify-content-center">
                                        <li className="breadcrumb-item">
                                            <Link to="/">Home</Link> {/* استخدام Link هنا */}
                                        </li>
                                        <li className="breadcrumb-item active" aria-current="page">
                                            Office Cleaning
                                        </li>
                                    </ol>
                                </nav>
                            </div>
                        </div>
                    </div>
                </section>

                {/* باقي المحتوى */}
            </main>
        </div>
    )
}

export default ServicesDetail
