import React, { useContext, useState } from 'react';
import Navbar from '../Components/Navbar';
import Header from '../Store/Header';
import SubNavbar from '../Components/SubNavbar';
import { apiValue } from '../Data/AllData';
import { useCart } from 'react-use-cart'; // ✅ استدعاء مكتبة السلة لإضافة المنتج
import Swal from 'sweetalert2';

function AllStore() {
  const full = useContext(apiValue); // ✅ البيانات جاية من السياق
  const [search, setSearch] = useState("");
  const { addItem } = useCart(); // ✅ استدعاء دالة إضافة للسلة

  const getData = (event) => {
    setSearch(event.target.value);
  };

  return (
    <div>
      <Navbar />
      <SubNavbar />
      <Header />
      <br />
      <input
        type="text"
        placeholder="ابحث عن منتج"
        onChange={getData}
        value={search}
        className="form-control w-50 mx-auto mb-4"
      />

      <div className="container">
        <div className="row">
          {full.map((item) => {
            const match = item.title.toLowerCase().includes(search.toLowerCase());

            if (search === "" || match) {
              return (
                <div key={item.id} className='col-md-3 text-center mb-4'>
                  <div className="card h-100 shadow-sm" style={{ borderRadius: '15px' }}> {/* ✅ تنسيق الكارد */}
                    <img
                      src={item.image}
                      alt={item.title}
                      className="card-img-top img-fluid"
                      style={{
                        height: '300px',
                        objectFit: 'cover',
                        borderTopLeftRadius: '15px',
                        borderTopRightRadius: '15px',
                      }}
                    />
                    <div className="card-body d-flex flex-column">
                      <h5 className="card-title">{item.title}</h5>
                      <h6 className="card-subtitle mb-2 text-muted">ID: {item.id}</h6>
                      <p className="card-text fs-5 fw-bold text-success">
                        {Math.ceil(item.price)} جنية

                      </p>

                      <button
                        onClick={() => {
                          addItem(item); // ✅ دي بتضيف المنتج للسلة
                          Swal.fire({
                            title: "تم إضافة المنتج بنجاح 😎",
                            icon: "success",
                            timer: 1700,
                            showConfirmButton: false,
                          }); // ✅ دي رسالة التأكيد بعد الإضافة
                        }}
                        className="btn btn-success mt-2"
                      >
                        أضف إلى السلة
                      </button>
                      {/* ✅ زر إضافة للسلة */}
                    </div>
                  </div>
                </div>
              );
            } else {
              return null;
            }
          })}
        </div>
      </div>
    </div>
  );
}

export default AllStore;
