import React from 'react';
import { NavLink } from 'react-router-dom';
import { useCart } from "react-use-cart";

function Navbar() {
    const { totalItems } = useCart();

    return (
        <div>
            <nav className="navbar navbar-expand-lg navbar-light bg-light">
                <div className="container">
                    <NavLink className="navbar-brand d-flex align-items-center" to="/">
                        <img src="images/bubbles.png" className="logo img-fluid" alt="Clean Work Logo" width="40" />
                        <span className="ms-2 fw-bold">Clean Work</span>
                    </NavLink>
                    <button
                        className="navbar-toggler"
                        type="button"
                        data-bs-toggle="collapse"
                        data-bs-target="#navbarNav"
                        aria-controls="navbarNav"
                        aria-expanded="false"
                        aria-label="Toggle navigation"
                    >
                        <span className="navbar-toggler-icon" />
                    </button>
                    <div className="collapse navbar-collapse" id="navbarNav">
                        <ul className="navbar-nav ms-auto align-items-center">
                            <li className="nav-item">
                                <NavLink
                                    to="/"
                                    className={({ isActive }) =>
                                        isActive ? "nav-link active fw-bold" : "nav-link"
                                    }
                                >
                                    Home
                                </NavLink>
                            </li>
                            <li className="nav-item">
                                <NavLink
                                    to="/about"
                                    className={({ isActive }) =>
                                        isActive ? "nav-link active fw-bold" : "nav-link"
                                    }
                                >
                                    About Us
                                </NavLink>
                            </li>
                            <li className="nav-item dropdown">
                                <a
                                    className="nav-link dropdown-toggle"
                                    href="#"
                                    id="navbarDropdown"
                                    role="button"
                                    data-bs-toggle="dropdown"
                                    aria-expanded="false"
                                >
                                    Pages
                                </a>
                                <ul
                                    className="dropdown-menu"
                                    aria-labelledby="navbarDropdown"
                                >
                                    <li>
                                        <NavLink
                                            to="/services"
                                            className={({ isActive }) =>
                                                isActive ? "dropdown-item active" : "dropdown-item"
                                            }
                                        >
                                            Our Services
                                        </NavLink>
                                    </li>
                                    <li>
                                        <NavLink
                                            to="/ComingSoon"
                                            className={({ isActive }) =>
                                                isActive ? "dropdown-item active" : "dropdown-item"
                                            }
                                        >
                                            Coming Soon
                                        </NavLink>
                                    </li>
                                </ul>
                            </li>
                            <li className="nav-item">
                                <NavLink
                                    to="/contact"
                                    className={({ isActive }) =>
                                        isActive ? "nav-link active fw-bold" : "nav-link"
                                    }
                                >
                                    Contact
                                </NavLink>
                            </li>
                            <li className="nav-item ms-2">
                                <NavLink
                                    to="/getstarted"
                                    className={({ isActive }) =>
                                        isActive
                                            ? "btn btn-primary rounded-pill px-3 active"
                                            : "btn btn-outline-primary rounded-pill px-3"
                                    }
                                >
                                    Get Started
                                </NavLink>
                            </li>
                        
                            <li className="nav-item">
                                <NavLink
                                    to="/store"
                                    className={({ isActive }) =>
                                        isActive ? "nav-link active fw-bold" : "nav-link"
                                    }
                                >
                                    🛍️ Market
                                </NavLink>
                            </li>
                            <li className="nav-item position-relative">
                                <NavLink
                                    to="/cart"
                                    className={({ isActive }) =>
                                        isActive ? "nav-link active fw-bold" : "nav-link"
                                    }
                                >
                                    🛒 Cart
                                    {totalItems > 0 && (
                                        <span className="position-absolute top-0 start-100 translate-middle badge rounded-pill bg-danger">
                                            {totalItems}
                                            <span className="visually-hidden">items in cart</span>
                                        </span>
                                    )}
                                </NavLink>
                            </li>
                        </ul>
                    </div>
                </div>
            </nav>
        </div>
    );
}

export default Navbar;