import React from 'react'
import Navbar from '../Components/Navbar'
import AllAbout from './AllAbout'
import Footer from '../Components/Footer'
import SubNavbar from '../Components/SubNavbar'

function About() {
  return (
    <div>
      <SubNavbar />
      <Navbar />
      <AllAbout />
      <Footer />
    </div>
  )
}

export default About